import { PropsWithChildren } from "react";
import PageContent from "./PageContent";
import { ITitleI18nConf } from ".";
export default function AndroidPageContent({ children, titleConf }: {titleConf?:ITitleI18nConf} & PropsWithChildren)  {
    return (
        <PageContent titleConf={titleConf}>
            {children}
        </PageContent>
    )
}
